.creatives-container {
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: column;
}

.users-grid {
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.user-card {
  border: 1.5px solid #000000;
  border-radius: 5px;
  padding: 10px;
  margin: 10px; /* Ajusta el margen según sea necesario */
  width: 250px; /* Ancho fijo para cada tarjeta */
  display: flex;
  flex-direction: column;
  align-items: center; /* Centra el contenido horizontalmente */
  cursor: pointer;
}

.user-image {
  width: 100%;
  max-height: 200px; /* Define una altura máxima */
  object-fit: cover;
  border-radius: 5px;
}

.user-details {
  margin-top: 10px;
  text-align: center; /* Centra el texto dentro de la tarjeta */
}
